    position: relative;
    background: white;
}

article img {
    max-width: 50%;
    max-height: 220px;
    margin: 10px;
}

.picture span {
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 15px 30px;
    background: #3c3c3c;
    color: lightgray;
    z-index: 1;
}

article button,
article a,
.add-form button,
.edit-form button,
.delete-item {
    font-family: 'Chakra Petch', sans-serif;
    font-size: 80%;
    max-width: 520px;
    padding: 10px 0;
    background: #fff;
    color: darkolivegreen;
    border: 1px solid lightgray;
    margin: 0 auto;
    line-height: 1.4em;
    text-decoration: none;
}

article button {
    margin-top: 20px;
}

.add-button,
.edit-button {
    max-width: 520px;
    margin: auto;
}

article button:hover,
article a:hover {
    background-color: #fff;
    color: rgb(0, 0, 0);
    cursor: pointer;
    font-size: 90%;
    text-decoration: none;
    box-shadow: 2px 2px 2px #3c3c3c;
    -webkit-transition: background-color 0.2s, color 0.2s, box-shadow 0.2s;
    transition: background-color 0.2s, color 0.2s, box-shadow 0.2s;
}

.add-form button,
.add-item,
.edit-form button,
.edit-item,
.delete-item {
    border-radius: 0;
    width: 100%;
}

.delete-item {
    display: block;
}

