.top-menu {
    /* border-bottom: 1px solid lightgray; */
    padding: 10px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background: #0223a6;
}

.logo {
    font-family: 'Audiowide', cursive;
    color: #ffeda4;
    display: inline-block;
    vertical-align: top;
}

.top-navigation {
    display: inline-block;
}

.content {
    margin: 0 auto;
}

.links {
    text-align: right;
}

.top-navigation li {
    display: inline-block;
    margin: 0 10px;
}

.top-navigation a,
.top-navigation button {
    text-decoration: none;
    border: 0;
    font-size: 16px;
    font-family: 'Chakra Petch', sans-serif;
    color: #ffeda4;
    margin: 0 25px;
    background-color: #0223a6;
}

.top-navigation button:hover {
    color: lightgrey;
    font-size: 17px;
}

@media all and (max-width: 620px) {
    .logo {
        font-size: 16px;
    }
    .top-navigation button {
        margin-right: 15px;
    }
    .top-navigation a,
    .top-navigation button {
        margin: 0;
        padding: 0;
        font-size: 11px;
        margin-right: 10px;
    }
}